







import Vue from 'vue'
import { mapGetters, mapActions } from 'vuex';


export default Vue.extend({
  name: "audioComponent",
  data() {
    return {
      audio: false,
      audioInterval: 0,
      toggled: false
    }
  },
  methods: {
    ...mapActions(["pauseAudio", "playAudio", "playSong"]),
    toggleAudio() {
      if(this.toggled === false) {
        this.initSong();
      }
      if(this.audio === false) {
        this.pauseAudio();
      }else {
        this.playAudio();
      }

      this.audio = !this.audio;

    },
    initSong() {
      this.toggled = true;

      this.pauseAudio();
      setTimeout(()  => {
        this.playSong();
        this.audioInterval = setInterval(() => { this.playSong(); }, 145000);
      }, 1000);
    }
  },
  computed: mapGetters(['mainSong']),
  mounted() {
    this.playAudio();
  }
  
})
