



















import Vue from 'vue'
import ChangeShip from '@/components/changeShip/ChangeShip.vue';

export default Vue.extend({
  name: "CustomizationMenu",
  components: {
    ChangeShip
  },
  data() {
    return  {
      selected: false,
      changeShip: false,
    }
  },
  methods: {
    toggleShip() {
      this.selected = !this.selected;
      this.changeShip = !this.changeShip
    }
  }
})
